/*------------------------------------*\
    # Simple Typography Settings
\*------------------------------------*/
ul,
ol {
  margin: 20px 0 0 20px; }
  ul li,
  ol li {
    margin-top: 10px;
    line-height: 1.5em;
    color: #000000; }
  ul ul,
  ul ol,
  ol ul,
  ol ol {
    margin-top: 0; }

ul li {
  list-style: disc; }

ul ul li {
  list-style: circle; }

ol li {
  list-style: decimal; }

hr {
  height: 1px;
  margin: 30px 0;
  border: none;
  background-color: #DADADA; }

blockquote {
  margin: 30px 0;
  font-size: 1.4375rem;
  line-height: 1.5em;
  color: #505050;
  text-align: center;
  font-style: italic; }

table {
  width: 100%;
  margin: 30px 0;
  border-collapse: collapse; }

table, td, th {
  border: 1px solid #DADADA;
  text-align: left; }

th {
  font-weight: 800; }

th, td {
  padding: 15px; }

figure {
  margin: 30px 0; }
  figure img {
    margin: 0; }

figcaption {
  margin-top: 10px;
  color: #1a1a1a;
  font-size: 16px; }

pre {
  display: block;
  margin: 0 0 40px 0;
  padding: 20px;
  background-color: #f9f9f9;
  border: 1px solid #DADADA;
  overflow: auto;
  border-radius: 3px; }
  pre code {
    position: static;
    padding: 0;
    border: none;
    line-height: 1.5em; }

code {
  position: relative;
  top: -0.2em;
  padding: 3px;
  font-family: Courier New, Courier, monospace;
  font-size: 0.88542rem;
  color: #282828;
  line-height: 1em;
  pointer-events: none;
  border: 1px solid #DADADA;
  border-radius: 3px; }

em,
i,
.italic {
  font-style: italic; }

strong,
b,
.bold {
  font-weight: 800; }

img {
  display: block;
  max-width: 100%;
  margin: 30px 0; }

.btn {
  display: inline-block;
  text-decoration: none;
  align-self: center;
  white-space: nowrap;
  border-radius: 30px;
  font-family: "Varela Round", sans-serif;
  font-size: 14px;
  margin-top: 20px;
  padding: 7px 14px 6px 14px;
  font-weight: bold;
  color: #000000;
  background: #ffffff;
  transition: all 0.25s;
  cursor: pointer; }
  .btn:hover {
    background: #ffeb3b;
    color: #000000; }

.wrapper {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3; }
  .wrapper.click-through {
    pointer-events: none; }

.brand {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: flex-end;
  flex: 0 0 180px; }
  @media (max-width: 600px) {
    .brand {
      flex: 0 0 140px; } }
  .brand img {
    width: 120px; }
    @media (max-width: 600px) {
      .brand img {
        width: 90px; } }

.intro-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 0 auto;
  color: #ffffff;
  animation: slideIn 2s;
  animation-fill-mode: forwards;
  animation-iteration-count: 1; }
  .intro-content > div {
    text-align: center;
    max-width: 900px;
    margin-bottom: 50px; }
    @media (max-width: 599px) {
      .intro-content > div {
        padding: 0 20px;
        margin-bottom: 30px; } }
    @media (max-width: 900px) {
      .intro-content > div {
        padding: 0 50px; } }

.skills-list {
  max-width: 800px;
  margin: 0 auto; }
  .skills-list ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 0;
    margin: 0; }
    .skills-list ul li {
      flex: 0 0 auto;
      list-style: none;
      margin: 0 8px 14px 8px;
      padding: 0 8px;
      white-space: nowrap;
      border-radius: 30px;
      font-family: "Varela Round", sans-serif;
      font-size: 14px;
      color: #ffffff;
      background: rgba(255, 255, 255, 0.3); }
      @media (max-width: 600px) {
        .skills-list ul li {
          font-size: 12px;
          margin: 0 8px 10px 8px; } }
      .skills-list ul li.more-info {
        margin-top: 20px;
        padding: 2px 10px;
        font-weight: bold;
        color: #000000;
        background: rgba(255, 255, 255, 0.6); }
        @media (max-width: 600px) {
          .skills-list ul li.more-info {
            margin-top: 10px; } }

.lightSwitch {
  position: fixed;
  top: 20px;
  left: 20px;
  padding: 20px;
  height: 30px;
  width: 30px;
  color: #ffffff; }

.social-footer {
  display: flex;
  padding: 0 0 20px 0;
  width: 100%;
  height: 50px;
  justify-content: center; }
  @media (max-width: 600px) {
    .social-footer {
      height: 40px; } }
  .social-footer__icon {
    width: 32px;
    height: 32px;
    margin: 0 10px;
    color: #ffffff;
    border-radius: 100%;
    background-color: rgba(255, 255, 255, 0.2);
    transition: background 0.3s, color 0.3s; }
    .social-footer__icon:hover {
      background: #ffffff;
      color: #000000; }

@keyframes slideIn {
  from {
    opacity: 0;
    margin-top: 50px; }
  to {
    opacity: 1;
    margin-top: 0; } }

